/* Main Page CSS */

:root {
    --bg-color: #FFF;
}

body {
    background-color: var(--bg-color);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

#ModalWrapper {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 100;
    overflow: hidden;
    display: flex;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:5px;
}

#ModalContent {
    max-width: 100%;
    max-height: 100%;
    min-width: 150px;
    min-height: 100px;
    background-color: #AAA;
    color: black;
    padding: 10px;
    overflow:auto;
}

button {
    cursor: pointer;
}

table, tr, th, td {
    border: 1px solid black;
    padding: 5px;
    background-color: #FFF;
}

table {
    table-layout: fixed;
    min-width: 100px;
    border-collapse: collapse;
}

th {
    font-weight: bold;
    background-color: #CCC;
}

.embed {
    width: 400px;
    height: 250px;
    margin: 2px;
}

.add-row {
    width: 100%;
}

/* INTERFACE */

#UI_TABLE td {
    vertical-align: middle;
    padding: 5px;
}

#UI_TABLE td input {
    width: calc(100% - 10px);
}

#UI_TABLE td.button-wrapper {
    padding: 0;
    border: none;
}

#UI_TABLE button.add-row, #UI_TABLE button.delete-row {
    display: block;
    margin: 0 auto;
    border: none;
    padding: 1px;
    background-color: #CCC;
    height: 100%;
    width: 100%;
}

#UI_TABLE button.add-row:hover {
    background-color: #ACF;
}

#UI_TABLE button.delete-row:hover {
    background-color: #FAA;
    --bg-color: #F00;
}

#UI_TABLE td.delete-row {
    height: 40px;
}

#TextInput {
    min-width: 400px;
    min-height: 250px;
}